import React from 'react';
import { slide as Menu } from 'react-burger-menu';

const styleBurgerMenu = {
    bmBurgerButton: {
        position: 'fixed',
        width: '36px',
        height: '30px',
        right: '24px',
        top: '24px',
        color: 'grey',
    },
    bmBurgerBars: {
        background: '#373a47',
    },
    bmBurgerBarsHover: {
        background: '#a90000',
    },
    bmCrossButton: {
        height: '24px',
        width: '24px',
    },
    bmCross: {
        display: 'none',
    },
    bmMenuWrap: {
        position: 'fixed',
        height: 'auto',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        width: 'auto',
        zIndex: '100',
        top: '0px',
    },
    bmMenu: {
        background: 'black',
        padding: '1.5em 1.5em 1.5em',
        fontSize: '1.4em',
    },
    bmMorphShape: {
        fill: '#373a47',
    },
    bmItemList: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        color: 'black',
        padding: '0.8em',
    },
    bmItem: {
        display: 'inline-block',
        color: 'white',
        textDecoration: 'none',
        paddingBottom: '1em',
        '&:focus': {
            outlineOffset: 'none',
            color: 'red',
        },
        '&:active': {
            border: '0',
            margin: '0',
            textDecoration: 'none',
            color: 'grey',
        },
        '&:hover': {
            backgroundColor: 'red',
        },
    },
    bmItemHover: {
        color: '#797997',
        backgroundColor: 'red',
        outlineOffset: 0,
    },
    bmOverlay: {
        display: 'none',
    },
};

export default function BurgerMenu() {
    return (
        <>
            <Menu
                right
                disableAutoFocus
                styles={styleBurgerMenu}
                customBurgerIcon={
                    <img alt='svgImg' src='data:image/svg+xml;base64,' />
                }
            >
                <a id='whatWeDo' href='/'>What we do</a>
                <a id='cases' href='/about'>Cases</a>
                <a id='services' href='/contact'>Services</a>
                <a id='whyUs' href='/contact'>Why us</a>
                <a id='pricing' href='/contact'>Pricing</a>
                <a id='contacts' href='/contact'>Contacts</a>
            </Menu>
        </>
    );
}
